import axios from '../../plugins/axios.js'

const state = {
	detail: {}
}

const actions = {
	detail:({
		// state,
		commit
	}, {
		_id,
		collectionName
	}) => {
		axios({
			// axios({
			url: `/news/${collectionName}/${_id}`,
		}).then(
			res => commit('detail', res.data.data)
		)
	}
}

const mutations = {
	detail: (state, payload) => state.detail = payload
}

export default {
	namespaced: true,
	state,
	actions,
	mutations
}
