import axios from '../../plugins/axios.js'

const state={
	follow:[]
}

const actions={
	follow:({state,commit},payload)=>{
		axios({
		// axios({
		  url:'/news/follow',
		  params:{
		    _limit: 20
		  },
		}).then(
		  res=>commit('follow',res.data.data)
		)
	}
}

const mutations={
	follow:(state,payload)=>state.follow=payload
}

export default {
	namespaced:true,
	state,actions,mutations
}
