<template>

	<div id="app">
		<van-pull-refresh v-model="isLoading" :head-height="80" @refresh="onRefresh">
			<template #pulling="props">
				<img class="doge" src="https://img01.yzcdn.cn/vant/doge.png"
					:style="{ transform: `scale(${props.distance / 80})` }" />
			</template>
		
			<!-- 释放提示 -->
			<template #loosing>
				<img class="doge" src="https://img01.yzcdn.cn/vant/doge.png" />
			</template>
		
			<!-- 加载提示 -->
			<template #loading>
				<img class="doge" src="https://img01.yzcdn.cn/vant/doge-fire.jpg" />
			</template>
		</van-pull-refresh>
		<loading v-show="$store.state.bLoading" />
		<app-header v-if="$store.state.bNav"></app-header>
		<router-view></router-view>
		<app-footer v-show="$store.state.bFoot"></app-footer>
	</div>

</template>

<script>
	import AppHeader from './header.vue'
	import AppFooter from './footer.vue'
	import loading from '../components/loading/loading.vue'

	export default {

		name: 'App',
		data() {
			return {
				count: 0,
				isLoading: false, 	
			}
		},
		components: {
			AppHeader,
			AppFooter,
			loading
		},
		methods: {
			onRefresh() {
				console.log(123);
				setTimeout(() => {
					// Toast('刷新成功');
					this.isLoading = false;
					this.count++;
				}, 1000);
			},
		},
		watch: {
			$route: {
				handler(nextValue) {
					console.log('watch');
					let path = nextValue.path; //String
					if (/home|follow|column/.test(path)) {
					// this.$root.bNav = this.$root.bFoot = true;
						this.$store.commit('nav',true)
						this.$store.commit('foot',true)
					}
					if (/detail|login|reg/.test(path)) {
					// this.$root.bNav = this.$root.bFoot = false;
					this.$store.commit('nav',false)
					this.$store.commit('foot',false)
					}
					if (/user/.test(path)) {
						this.$store.commit('nav',false)
						this.$store.commit('foot',true)
						// this.$root.bNav = false;
						// this.$root.bFoot = true;
					}
				},
				immediate: true
			}
		}

	}
</script>

<style>
	html{background: url(../../public/images/666.png);}
	.doge {
		width: 140px;
		height: 72px;
		margin-top: 8px;
		border-radius: 4px;
	}
</style>
