import axios from '../../plugins/axios.js'

const state = {
	banner: [],
	home: [],
}

const actions = {
	banner: ({
		state,
		commit
	}, payload) => {
		axios({
			// axios({
			url: '/news/banner',
			params: {
				_limit: 3
			},
		}).then(
			res => commit('banner', res.data.data)
		)
	},
	home: ({
		state,
		commit
	}, payload) => {
		axios({
			// axios({
			url: '/news/home',
			params: {
				_limit: 3
			},
		}).then(
			res => commit('home', res.data.data)
		)
	}
}

const mutations = {
	banner: (state, payload) => state.banner = payload,
	home: (state, payload) => state.home = payload
}

export default {
	namespaced: true,
	state,
	actions,
	mutations
}
