<template>
	<div>
		<van-notice-bar left-icon="volume-o" text="在代码阅读过程中人们说脏话的频率是衡量代码质量的唯一标准。" />
		<div id="echarts" style="height:250px; top: 82px;"></div>
		<p style="position: absolute;top:286px;">我们的公司地址是：上海市宝山区同济支路第二工业大学七立方科技园3号楼千锋教育</p>
		<div id="map" style="height:300px ;top:70px"></div>
	</div>
</template>

<script>
	import * as echarts from 'echarts'

	export default {
		mounted() {

			let instance = echarts.init(document.getElementById('echarts'));

			var option = {
				title: {
					text: '睡前小故事30篇',
					link: 'http://www.baidu.com'
				},
				tooltip: {},
				legend: {
					data: ['阅读量']
				},
				xAxis: {
					data: ["捉迷藏", "三小猪", "拔萝卜", "龟兔赛", "小马过", "狐假虎"]
				},
				yAxis: {},
				series: [{
					name: '阅读量',
					type: 'bar',
					data: [500, 200, 360, 1000, 100, 200]
				}]
			};
			//渲染
			instance.setOption(option);


			var map = new window.BMapGL.Map('map'); // 创建Map实例
			map.centerAndZoom(new window.BMapGL.Point(121.49824642727945, 31.38456001767598), 16); // 初始化地图,设置中心点坐标和地图级别
			map.enableScrollWheelZoom(true); // 开启鼠标滚轮缩放

			var marker1 = new window.BMapGL.Marker(new window.BMapGL.Point(121.49824642727945, 31.38456001767598));
			map.addOverlay(marker1);
		}
	}
</script>

<style scoped>
	.van-notice-bar {
		position: absolute;
		top: 0.7rem;
		width: 6.2rem;
	}
</style>
