import Vue from 'vue'
import App from './layouts/App.vue'

import './assets/css/base.css'
import './assets/js/fonts.js'

Vue.config.productionTip = false
import store from './plugins/vuex.js'
import router from './plugins/router.js' //路由的配置

import './global' //引入所有全局配置

import axios from './plugins/axios'
Vue.prototype.$axios = axios;

import {
	baseUrl
} from './config';
Vue.prototype.$baseUrl = baseUrl; //配置接口服务器基础地址

import {
	Icon
} from 'vant'; //消息999+
Vue.use(Icon);

import {
	Rate
} from 'vant'; //评分
Vue.use(Rate);

import {
	PullRefresh
} from 'vant'; //下拉刷新
Vue.use(PullRefresh);

import { NoticeBar } from 'vant';

Vue.use(NoticeBar);  //NoticeBar 通知栏
    
import { IndexBar, IndexAnchor } from 'vant';//IndexBar 索引栏

Vue.use(IndexBar);
Vue.use(IndexAnchor);


import { Cell, CellGroup } from 'vant'; //Cell 单元格

Vue.use(Cell);
Vue.use(CellGroup);

//同步local
let user = window.localStorage.getItem('user');
let token = user ? JSON.parse(window.localStorage.getItem('user')).token : '';
//let token = user ? JSON.parse(window.localStorage.getItem('user')).token : ''

if (token) {
	store.commit('user/user', JSON.parse(user))
} else {
	store.commit('user/user', {
		err: 1,
		mag: '未登录'
	})
	}

	let vm = new Vue({
		render: h => h(App),
		router,
		store
	}).$mount('#app');

export default vm;