import axios from '../../plugins/axios.js'

const state={
	column:[]
}

const actions={
	column:({state,commit},payload)=>{
		axios({
		// axios({
		  url:'/news/column',
		  params:{
		    _limit: 10
		  },
		}).then(
		  res=>commit('column',res.data.data)
		)
	}
}

const mutations={
	column:(state,payload)=>state.column=payload
}

export default {
	namespaced:true,
	state,actions,mutations
}
